.login{
  position: relative;
  height: 100%;
  background: url("https://assets.nflxext.com/ffe/siteui/vlv3/538717c0-f271-40e9-a99e-b07f9c521322/785c7470-5017-4447-8845-b7eeaa9f2070/US-es-20210201-popsignuptwoweeks-perspective_alpha_website_small.jpg") center no-repeat;
  background-size: cover;
}

.login__gradient{
  width: 100%;
  z-index: 1;
  height: 100vh;
  background: rgba(0,0,0,0.4);
  background-image: linear-gradient(
      to top,
      rgba(0,0,0,0.8) 0,
      rgba(0,0,0,0) 60%,
      rgba(0,0,0,0.8) 100%
  )
}

.login__logo{
  position: fixed;
  left: 0;
  width: 150px;
  object-fit: contain;
  padding-left: 20px;
}


.login__button{
  position: fixed;
  top: 20px;
  right: 20px;
  padding: 10px 20px;
  font-size: 1rem;
  color: #fff;
  background-color: #e50914;
  font-weight: 600;
  border: none;
  cursor: pointer;
}


.login__body{
  position: absolute;
  width: 100%;
  top: 30%;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1;
  color: #fff;
  text-align: center;
}
.login__body > h1{
  font-size: 3.125rem;
  margin-bottom: 20px;
}

.login__body > h2{
  font-size: 2rem;
  font-weight: 400;
  margin-bottom: 30px;
}

.login__body > h3{
  font-size: 1.3rem;
  font-weight: 400;
}

.login__input{
  margin: 20px;
}

.login__input > form > input{
  padding: 10px;
  padding-bottom: 9px;
  outline-width: 0;
  height: 30px;
  width: 30%;
  border: none;
  max-width: 600px;
  
}

.login__getStarted{
  padding: 15.5px 20px;
  font-size: 1rem;
  color: #fff;
  background-color: #e50914;
  border: none;
  font-weight: 600;
  cursor: pointer;
}

@media(max-width: 667px){
  .login__body > h1{
      font-size: 2.2rem;
  }

  .login__body > h2{
      font-size: 1.5rem;
      font-weight: 400;
      margin-bottom: 20px;
  }
  
  .login__body > h3{
      font-size: 1.15rem;
  }

  .login__input{
      margin-right: 0px;
      margin-left: 0px;
  }

  .login__input > form > input{
      width: 50%;
      border: none;
  }

  .login__getStarted{
      font-size: 1rem;
      font-weight: 400;
      cursor: pointer;
  }
}

@media(max-width: 340px){
  .login__input > form > input{
      width: 90%;
      margin-bottom: 14px;
  }
}