.signUp{
    max-width: 300px;
    padding: 70px;
    margin: auto;
    background: rgba(0,0,0,0.85);
}

.signUp > form{
    display: grid;
    flex-direction: column;
}

.signUp > form > h1{
    text-align: left;
    margin-bottom: 25px;
}

.signUp > form > input {
    outline-width: 0;
    height: 40px;
    margin-bottom: 14px;
    border-radius: 5px;
    border: none;
    padding: 5px 15px;
}

.signUp > form > button{
    padding: 16px 20px;
    font-size: 1rem;
    color: #fff;
    border-radius: 5px;
    background-color: #e50914;
    font-weight: 600;
    border: none;
    cursor: pointer;
    margin-top: 20px;
}

.signUp > form > h4{
    text-align: left;
    margin-top: 30px;
}

.signUp__gray{
    color: gray;
}

.signUp__link:hover{
    cursor: pointer;
    text-decoration: underline;
}